import React from 'react';

export function usePersistentState(key, defaultValue) {
  const [state, setState] = React.useState(
    () => JSON.parse(localStorage.getItem(key)) || (typeof defaultValue === "function" ? defaultValue() : defaultValue)
  );
  React.useEffect(() => {
    localStorage.setItem(key, JSON.stringify(state));
  }, [key, state]);
  return [state, setState];
}

export const formatNumber = (x, pos = 0) => x.toFixed(pos).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
