import React from 'react';
import { usePersistentState, formatNumber } from './utils';

const RankRP = [0, 0, 2000, 5000, 10000, 15000, 20000, 25000, 30000, 35000, 40000, 45000, 50000, 55000, 60000];
const BracketRP = [0, 13000, 12000, 11000, 10000, 9000, 8000, 7000, 6000, 5000, 4000, 3000, 2000, 1000, 400];

const formatRp = rp => {
  let rank = 0;
  while (rank < 14 && rp >= RankRP[rank + 1]) {
    rank += 1;
  }
  if (rank === 14) {
    return `${rank}`;
  } else {
    return `${rank} (${(100 * (rp - RankRP[rank]) / (RankRP[rank + 1] - RankRP[rank])).toFixed(0)}%)`;
  }
}

export default function RankPlanner() {
  const [plan, setPlan] = usePersistentState("plan", []);
  const [current, setCurrent] = usePersistentState("current", 0);
  const [goal, setGoal] = usePersistentState("goal", 14);

  const weeks = [];
  let rp = current;
  while (rp < RankRP[goal]) {
    weeks.push(rp);
    if (plan.length < weeks.length) {
      plan.push(1);
    }
    let adjust = BracketRP[plan[weeks.length - 1]] - rp * 0.2;
    if (adjust < 0) {
      adjust = Math.max(adjust / 2, -2500);
    }
    rp += adjust;
  }
  plan.length = weeks.length;
  weeks.push(rp);

  const bracket = index => {
    if (index < plan.length) {
      const setBracket = value => {
        const newPlan = plan.slice();
        newPlan[index] = value;
        setPlan(newPlan);
      };
      return (
        <select value={plan[index]} onChange={e => setBracket(parseInt(e.target.value))}>
          {BracketRP.map((rp, br) => <option value={br} key={br}>{br || "None"}</option>)}
        </select>
      );
    } else {
      return "";
    }
  };

  const select = elt => {
    if (window.getSelection && document.createRange) {
      const sel = window.getSelection();
      const range = document.createRange();
      range.selectNodeContents(elt);
      sel.removeAllRanges();
      sel.addRange(range);
    }
  };

  return (
    <div className="RankPlanner">
      <div className="header">
       To find your current RP, use this macro (for rank 3 and higher):<br/>
       <code onClick={e => select(e.target)}>/script print("Current RP: "..math.floor(GetPVPRankProgress()*5000+(UnitPVPRank("player")-6)*5000))</code>
      </div>
      <div className="header">
        <span>Current RP: <input type="number" min={0} max={60000} step={1} value={current} onChange={e => setCurrent(parseInt(e.target.value || 0))}/></span>
        <span>Target rank: <input type="number" min={2} max={14} step={1} value={goal} onChange={e => setGoal(parseInt(e.target.value || 0))}/></span>
      </div>
      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>RP</th>
            <th>Rank</th>
            <th>Bracket</th>
          </tr>
        </thead>
        <tbody>
          {weeks.map((rp, index) => (
            <tr key={index}>
              <td>{index}</td>
              <td>{formatNumber(rp)}</td>
              <td>{formatRp(rp)}</td>
              <td>{bracket(index)}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
