import classNames from 'classnames';
import { usePersistentState } from './utils';

const HonorTable = {
  "48,1": [32, 2], "48,2": [34, 0], "48,3": [36, 0], "48,4": [38, 0], "48,5": [40, 0], "48,6": [42, 0], "48,7": [44, 0], "48,8": [47, 0],
  "49,1": [40, 2], "49,2": [42, 2], "49,3": [44, 0], "49,4": [47, 0], "49,5": [49, 0], "49,6": [52, 0], "49,7": [55, 0], "49,8": [58, 0], "49,9": [61, 0],
  "50,1": [49, 2], "50,2": [52, 0], "50,3": [55, 2], "50,4": [58, 0], "50,5": [61, 0], "50,6": [65, 0], "50,7": [68, 0], "50,8": [72, 0], "50,9": [76, 0],
  "51,1": [59, 2], "51,2": [62, 0], "51,3": [66, 0], "51,4": [69, 0], "51,5": [73, 0], "51,6": [77, 0], "51,7": [82, 0], "51,8": [86, 0], "51,9": [91, 0], "51,10": [96, 0],
  "52,1": [70, 2], "52,2": [74, 0], "52,3": [78, 0], "52,4": [82, 0], "52,5": [87, 0], "52,6": [92, 0], "52,7": [97, 0], "52,8": [102, 0], "52,9": [108, 0], "52,10": [114, 0],
  "53,1": [82, 2], "53,2": [87, 1], "53,3": [91, 0], "53,4": [96, 0], "53,5": [102, 0], "53,6": [107, 0], "53,7": [113, 0], "53,8": [120, 0], "53,9": [126, 0], "53,10": [133, 0], "53,11": [141, 0],
  "54,1": [95, 2], "54,2": [100, 2], "54,3": [106, 1], "54,4": [111, 0], "54,5": [118, 0], "54,6": [124, 0], "54,7": [131, 0], "54,8": [138, 0], "54,9": [146, 0], "54,10": [154, 0], "54,11": [163, 0],
  "55,1": [109, 2], "55,2": [115, 2], "55,3": [121, 2], "55,4": [128, 0], "55,5": [135, 0], "55,6": [142, 0], "55,7": [150, 0], "55,8": [159, 0], "55,9": [166, 0], "55,10": [177, 0], "55,11": [186, 0], "55,12": [197, 0],
  "56,1": [124, 2], "56,2": [131, 2], "56,3": [138, 0], "56,4": [146, 0], "56,5": [154, 0], "56,6": [163, 0], "56,7": [172, 0], "56,8": [181, 0], "56,9": [191, 0], "56,10": [202, 0], "56,11": [213, 0], "56,12": [225, 0],
  "57,1": [141, 2], "57,2": [148, 2], "57,3": [156, 0], "57,4": [165, 0], "57,5": [174, 0], "57,6": [184, 0], "57,7": [194, 0], "57,8": [205, 0], "57,9": [216, 0], "57,10": [228, 0], "57,11": [241, 0], "57,12": [254, 0], "57,13": [268, 0],
  "58,1": [159, 2], "58,2": [168, 0], "58,3": [176, 2], "58,4": [186, 0], "58,5": [196, 0], "58,6": [207, 0], "58,7": [218, 0], "58,8": [231, 0], "58,9": [243, 0], "58,10": [257, 0], "58,11": [271, 0], "58,12": [286, 0], "58,13": [302, 0],
  "59,1": [178, 2], "59,2": [188, 2], "59,3": [198, 0], "59,4": [209, 0], "59,5": [221, 0], "59,6": [233, 0], "59,7": [246, 0], "59,8": [260, 0], "59,9": [274, 0], "59,10": [290, 0], "59,11": [306, 0], "59,12": [323, 0], "59,13": [341, 0], "59,14": [360, 0],
  "60,1": [199, 2], "60,2": [210, 2], "60,3": [221, 2], "60,4": [233, 2], "60,5": [246, 2], "60,6": [260, 2], "60,7": [274, 2], "60,8": [289, 2], "60,9": [305, 1], "60,10": [321, 2], "60,11": [339, 2], "60,12": [358, 2], "60,13": [377, 2], "60,14": [398, 2],
};
const certainty = ["uncertain", "probably", "confirmed"];
const defaultUsed = () => {
  const used = {};
  for (const [key, [, certainty]] of Object.entries(HonorTable)) {
    const [level, rank] = key.split(",").map(str => parseInt(str));
    if (certainty === 2 && (level === 60 || rank === 1)) {
      used[key] = true;
    }
  }
  return used;
};

const RankH = ["Scout", "Grunt", "Sergeant", "Senior Sergeant", "First Sergeant", "Stone Guard", "Blood Guard", "Legionnaire", "Centurion", "Champion", "Lieutenant General", "General", "Warlord", "High Warlord"];
const RankA = ["Private", "Corporal", "Sergeant", "Master Sergeant", "Sergeant Major", "Knight", "Knight-Lieutenant", "Knight-Captain", "Knight-Champion", "Lieutenant Commander", "Commander", "Marshal", "Field Marshal", "Grand Marshal"];
const RankNames = {horde: RankH, alliance: RankA};

const range = (a, b) => [...Array(b - a + 1)].map((v, i) => a + i);
const MaxHonor = 500;

export default function HonorCap() {
  const [used, setUsed] = usePersistentState("honorUsed", defaultUsed);
  const [faction, setFaction] = usePersistentState("faction", "horde");
  const [needed, setNeeded] = usePersistentState("needed", 0);
  if (needed > MaxHonor) {
    setNeeded(MaxHonor);
  }
  const [hover, setHover] = React.useState([]);

  const table = React.useMemo(() => {
    const result = range(0, MaxHonor).map(() => []);
    for (let [key, [honor]] of Object.entries(HonorTable)) {
      if (used[key]) {
        for (let ph = 0; ph + honor <= MaxHonor; ++ph) {
          if (ph === 0 || result[ph].length) {
            result[ph + honor].push(honor);
          }
        }
      }
    }
    console.log(result, used);
    return result;
  }, [used]);

  const [honor, methods] = React.useMemo(() => {
    let honor = needed;
    while (honor > 0 && (!table[honor] || !table[honor].length)) {
      --honor;
    }
    const methods = [];
    const list = (h, cap, prefix) => {
      if (h === 0) {
        if (prefix.length > 0) {
          methods.push(prefix);
        }
        return;
      }
      for (let nh of table[h]) {
        if (nh <= cap) {
          list(h - nh, nh, [...prefix, nh]);
        }
      }
    };
    list(honor, MaxHonor, []);
    methods.sort((a, b) => {
      for (let i = 0; i < a.length || i < b.length; ++i) {
        if (a[i] !== b[i]) {
          return (b[i] || 0) - (a[i] || 0);
        }
      }
      return 0;
    });
    return [honor, methods];
  }, [table, needed]);

  return (
    <div className="HonorCap">
      <div className="header">
        Only highlighted cells will be used in calculations. Click on cells to toggle selection.
      </div>
      <table className="honorTable">
        <thead>
          <tr>
            <th><select value={faction} onChange={e => setFaction(e.target.value)}><option value="horde">Horde</option><option value="alliance">Alliance</option></select></th>
            {RankNames[faction].map((name, index) => <th key={index}><span title={name}>{name}</span></th>)}
          </tr>
          <tr>
            <th>Level</th>
            {RankNames[faction].map((name, index) => <th key={index}>{index + 1}</th>)}
          </tr>
        </thead>
        <tbody>
          {range(48, 60).map(level => (
            <tr key={level}>
              <td>{level}</td>
              {range(1, 14).map(rank => {
                const key = `${level},${rank}`;
                const value = HonorTable[key];
                if (value) {
                  const toggle = () => setUsed({...used, [key]: !used[key]});
                  return <td key={rank} className={classNames(certainty[value[1]], {used: used[key], hover: hover.includes(value[0])})} title={certainty[value[1]]} onClick={toggle}>{value[0]}</td>
                } else {
                  return <td key={rank}/>;
                }
              })}
            </tr>
          ))}
        </tbody>
      </table>
      <div>Honor needed: <input type="number" min={0} max={MaxHonor} step={1} value={needed} onChange={e => setNeeded(parseInt(e.target.value || 0))}/>
        {honor < needed && honor > 0 && <span className="warning">(closest possible honor: {honor})</span>}
      </div>
      <div className="results">
        <ul>
          {methods.length === 0 && <li className="empty">Honor below lowest value</li>}
          {methods.length > 0 && methods.map((method, index) => {
            const short = [];
            for (let h of method) {
              if (short.length && short[short.length - 1][0] === h) {
                short[short.length - 1][1] += 1;
              } else {
                short.push([h, 1]);
              }
            }
            return (
              <li key={index} onMouseEnter={() => setHover(method)} onMouseLeave={() => setHover([])}>
                {short.map(([h, c], i) => (
                  <span key={h}>{i > 0 && " + "}{h}{c > 1 && ` x${c}`}</span>
                ))}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}
